body {
  margin: 0;
  font-family: -apple-system, BlinkMacSystemFont, "Segoe UI", "Roboto", "Oxygen",
    "Ubuntu", "Cantarell", "Fira Sans", "Droid Sans", "Helvetica Neue",
    sans-serif;
  -webkit-font-smoothing: antialiased;
  -moz-osx-font-smoothing: grayscale;
}

body {
  background: white;
  font-family: "Open Sans", Helvetica Neue, Helvetica, Arial, sans-serif;
  color: black;
  padding-left: 280px;
}

.right {
  position: relative;
}

.left {
  background: #4c56c0;
  height: 100%;
  width: 280px;
  position: fixed;
  top: 0;
  left: 0;
  z-index: 5;
  outline: none;
  border-top-right-radius: 2em;
  border-bottom-right-radius: 2em;
  color: #ffffff;
}

ul {
  list-style: none;
  padding: 0.7em 0 0 0;
  margin: 0;
}

ul li {
  margin: 0;
  padding: 0;
  height: 48px;
  background-repeat: no-repeat;
  background-position: left 15px center;
  background-size: auto 20px;
  transition: all 0.15s linear;
  cursor: pointer;
  text-transform: uppercase;
}

ul li a {
  display: block;
  margin-left: 0.6em;
  padding: 0.9em 1em 0.9em 1.7em;
}

ul li a svg {
  width: 28px;
}

ul li a span {
  padding-left: 15px;
}

ul li a:link,
ul li a:visited,
ul li a:hover,
ul li a:active {
  text-decoration: none;
  color: #ffffff;
}

ul li a.active {
  text-decoration: none;
  background-color: #f5f7fb;
  border-top-left-radius: 2em;
  border-bottom-left-radius: 2em;
  color: #4c56c0;
}

ul li:focus {
  outline: none;
}

.footer {
  padding-bottom: 2em;
  position: absolute;
  bottom: 0;
  left: 0;
  width: 100%;
  text-align: center;
}

.user-avatar {
  height: 133px;
  padding: 3em 0;
  text-align: center;
}

.user-avatar img {
  width: 82px;
  border-radius: 50%;
  overflow: hidden;
}

.user-avatar h2 {
  margin-top: 0;
  margin-bottom: 0;
  font-weight: normal;
  text-transform: uppercase;
}

.header-block {
  padding: 1.6em 0.9em;
}

.block {
  margin: 0 0.9em;
  box-sizing: border-box;
  display: inline-block;
  height: 6.5em;
  width: 17em;
  background: #4c56c0;
  color: #ffffff;
  border-top-right-radius: 1em;
  border-bottom-right-radius: 1em;
  border-top-left-radius: 1em;
  border-bottom-left-radius: 1em;
  vertical-align: top;
}

.block-green {
  background: #9dd6ab;
}

.text {
  font-weight: bold;
  padding: 1em 1em;
  text-align: left;
}

.name-cell {
  width: 12em;
}

.octocat {
  width: 110px;
}

.octocat-container {
  display: flex;
}

.octocat-text {
  font-size: 20px;
  width: 12em;
  text-align: center;
}

